.header_outer_container {
  position: sticky;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  /* background-color: rgb(148, 132, 132); */
  background-color: rgb(20, 20, 20);
  background-color: transparent;
  height: 10vh;
  color: #ffff;
  transition: all 0.5s;
  transition-timing-function: ease-in;
  z-index: 1;
  background-color: black;
}
.nav_black {
  background-color: #111;
}
.header_container {
  width: 90%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header_list_left {
  width: 140%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style: none;
  cursor: pointer;
}
.header_list_right {
  width: 120%;
  display: flex;
  justify-content: space-between;
  list-style: none;
  cursor: pointer;
}
